body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,h2,h3,h4,h5,h6,input,label,p{color:#393636}*{padding:0;margin:0;box-sizing:border-box;text-decoration:none;list-style-type:none}.App{background-color:#fff}main{display:grid;grid-template-columns:repeat(12, 1fr);grid-template-rows:repeat(8, 1fr);height:100vh;background-color:#fff}
h1,h2,h3,h4,h5,h6,input,label,p{color:#393636}.nav{background-color:#fff;grid-area:1/1/1/13;display:flex;align-content:flex-end;justify-content:space-around;align-items:center;height:7.5vh;box-shadow:0 8px 6px -6px rgba(34,0,0,.109)}.nav__logo{height:60%}.nav__hamburger{stroke:#f9f6fd}@media only screen and (min-width: 600px){.nav__hamburger{display:none}}.nav__container{display:flex;width:35vw;list-style-type:none;justify-content:space-between;color:#fff;font:"Exo 2",sans-serif}@media only screen and (max-width: 600px){.nav__container{display:none}}.nav__link{font-weight:600;font-size:1rem;color:#393636;font-family:"Exo 2",sans-serif;text-decoration:none}.nav__link a{text-decoration:none}.nav__link a:visited{text-decoration:none}.nav__link a:hover{text-decoration:underline;color:#9e005d}
.lift{grid-area:3/2/3/6}.lift__list{display:grid;grid-template-rows:repeat(3, 1fr);grid-template-columns:repeat(2, 1fr);height:10vh}@media only screen and (max-width: 670px){.lift__list{display:flex;flex-direction:column}}.lift .radio__label{width:100%}
h1,h2,h3,h4,h5,h6,input,label,p{color:#393636}.weightcontainer .weight__input{display:flex;align-items:center;height:50%}.weightcontainer .weight__input .input{width:75%;height:50%}.weightcontainer .weight__input .input__kgs{background-color:#d3d2d2;height:50%;color:#fff;padding:1px 2px;flex:1 1;display:flex;align-items:center;justify-content:center}

h1,h2,h3,h4,h5,h6,input,label,p{color:#393636}.slider-labels{display:flex;justify-content:space-between}.slider{width:100%;-webkit-appearance:none;height:3vh;border-radius:5px;background:#d3d2d2;outline:none;opacity:.7;transition:opacity .2s}.slider:hover{opacity:1}.slider--moderate::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/react-powerlifting-tracker/static/media/moderate.b9baba29.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:40px;height:40px;cursor:pointer}.slider--moderate::-moz-range-thumb{width:23px;height:25px;border:0;background:url(/react-powerlifting-tracker/static/media/moderate.b9baba29.png);cursor:pointer}.slider--easy::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/react-powerlifting-tracker/static/media/easy.bc5aa1f7.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:40px;height:40px;cursor:pointer}.slider--easy::-moz-range-thumb{width:23px;height:25px;border:0;background:url(/react-powerlifting-tracker/static/media/easy.bc5aa1f7.png);cursor:pointer}.slider--very-easy::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/react-powerlifting-tracker/static/media/veryEasy.4b359e02.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:40px;height:40px;cursor:pointer}.slider--very-easy::-moz-range-thumb{width:23px;height:25px;border:0;background:url(/react-powerlifting-tracker/static/media/veryEasy.4b359e02.png);cursor:pointer}.slider--hard::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/react-powerlifting-tracker/static/media/hard.8159f55d.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:40px;height:40px;cursor:pointer}.slider--hard::-moz-range-thumb{width:23px;height:25px;border:0;background:url(/react-powerlifting-tracker/static/media/hard.8159f55d.png);cursor:pointer}.slider--very-hard::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;border:0;border-radius:50%;background-image:url(/react-powerlifting-tracker/static/media/veryHard.c18ad77f.png);background-size:contain;background-position:center center;background-repeat:no-repeat;width:40px;height:40px;cursor:pointer}.slider--very-hard::-moz-range-thumb{width:40px;height:40px;border:0;background:url(/react-powerlifting-tracker/static/media/veryHard.c18ad77f.png);cursor:pointer}
h1,h2,h3,h4,h5,h6,input,label,p{color:#393636}.btn{background-color:#29abe2;color:#393636;border:none;border-radius:5vmin;width:15%;height:8%;font-size:1rem;font-family:"Open Sans",sans-serif;font-weight:100;box-shadow:0px 10px 10px -6px rgba(34,0,0,.315)}@media only screen and (max-width: 1360px){.btn{width:40%}}.btn:hover{background-color:#9e005d;color:#fff}
h1,h2,h3,h4,h5,h6,input,label,p{color:#393636}.form{background-color:#f9f6fd;padding:5vmin;border-radius:19px;grid-area:2/2/9/12;margin:5vmin;box-shadow:0 8px 6px -6px rgba(34,0,0,.11);font-family:"Open Sans",sans-serif;display:flex;flex-direction:column;align-items:stretch;justify-content:space-around}.form .title{font-weight:600;font-family:"Exo 2",sans-serif}.form .weight__input{height:10vh}.form .weight__input .input{background-color:#f9f6fd;border:#d3d2d2 1px solid;border-top-left-radius:.5vmin;border-bottom-left-radius:.5vmin;z-index:1}
h1,h2,h3,h4,h5,h6,input,label,p{color:#393636}.card-container{grid-area:3/3/8/12;display:flex;flex-wrap:wrap}
h1,h2,h3,h4,h5,h6,input,label,p{color:#393636}.card{display:flex;flex-direction:column;background-color:#f9f6fd;padding:2.5vmin;margin:.5vmin;border-radius:1rem;height:45vh;align-content:flex-end;flex-wrap:nowrap;justify-content:center;align-items:center;box-shadow:0 8px 6px -6px rgba(34,0,0,.109)}@media only screen and (min-width: 641px){.card{width:20vw;height:25vh}}@media only screen and (max-width: 640px){.card{width:80vw;height:25vh}}.card__text{display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(3, 1fr);flex-grow:1;width:100%}.card__title{width:100%;font-size:1rem;font-family:"Exo 2",sans-serif;font-weight:600}.card__info{width:100%;font-size:.75rem;padding:0px;margin:0px;display:flex;align-items:center;font-family:"Open Sans",sans-serif}.card__image{width:30%;height:30%}
.liftselect{width:50%;height:50%}.filter{display:flex;flex-direction:column}.filter__label{padding-bottom:1vmin}
.filters{grid-area:2/2/2/13;width:100vw;display:flex;flex-direction:row;align-items:center;justify-content:space-around}.filters .difficulty-container{width:50%}.filters .difficulty-container .difficulty-filter{width:100%}
